import React from "react";
import IndexPage from "@components/page/IndexPage";
import { SeoContainer } from "@components/Container/seoContainer";
import { graphql } from "gatsby";
import WorkInProgress from "@components/WorkInProgress";
import styled from "styled-components";

const ResumeLayout = styled.div.withConfig({ componentId: "ResumeLayout" })`
  height: calc(100vh - 70px);
  iframe {
    width: 100%;
    height: 100%;
  }
`;

const Index = () => (
  <ResumeLayout>
    <iframe src="https://www.cakeresume.com/dennnny" />
  </ResumeLayout>
);

export default Index;

export const Head = () => (
  <SeoContainer>
    <meta
      name="google-site-verification"
      content="7DkPSaWn_BaOEvNXuBL2Du0jBY_-ZWg1wf52fcGvq8w"
    />
  </SeoContainer>
);

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
